import { Badge } from '../ui/badge';

const statusConfig = {
  open: {
    variant: 'warning',
    label: 'Ak',
  },
  'in-progress': {
    variant: 'default',
    label: 'leniyor',
  },
  resolved: {
    variant: 'success',
    label: 'zld',
  },
  closed: {
    variant: 'secondary',
    label: 'Kapal',
  },
};

export const StatusPill = ({ status }) => {
  const config = statusConfig[status] || statusConfig.open;
  
  return (
    <Badge variant={config.variant}>
      {config.label}
    </Badge>
  );
};
