import { Check } from 'lucide-react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card';
import { Button } from '../components/ui/button';
import { toast } from 'sonner';
import { createCheckoutSession } from '../lib/api';
import { useState } from 'react';

const plans = [
  {
    name: 'Free',
    price: '0',
    period: '/ay',
    description: 'Kk ekipler iin balamak iin idealdir',
    features: [
      'Ayda 100 ticket',
      'Email destei',
      'Temel raporlama',
      '1 kullanc',
    ],
    highlighted: false,
  },
  {
    name: 'Pro',
    price: '299',
    period: '/ay',
    description: 'Byyen ekipler iin en iyi seenek',
    features: [
      'Snrsz ticket',
      'ncelikli destek',
      'Gelimi raporlama',
      '10 kullancya kadar',
      'AI zellikleri',
      'API eriimi',
    ],
    highlighted: true,
  },
  {
    name: 'Enterprise',
    price: 'zel',
    period: '',
    description: 'Byk kurulular iin zellenebilir zmler',
    features: [
      'Tm Pro zellikleri',
      'Snrsz kullanc',
      'zel entegrasyonlar',
      'Hesap yneticisi',
      'SLA garantisi',
      'Kurumsal destek',
    ],
    highlighted: false,
  },
];

export default function Billing() {
  const [loading, setLoading] = useState(null);
  
  const handleSelectPlan = async (planName) => {
    const planId = planName.toLowerCase();
    
    // For now, disabled for real checkout
    if (planName === 'Enterprise') {
      toast.info('Kurumsal plan iin ltfen sat ekibimizle iletiime gein');
      return;
    }
    
    setLoading(planId);
    
    try {
      const { checkoutUrl } = await createCheckoutSession(planId);
      
      // Mock: Show checkout URL instead of redirecting
      toast.success(`${planName} plan seildi (Mock checkout URL hazr)`);
      console.log('Checkout URL:', checkoutUrl);
      
      // REAL: Redirect to Stripe checkout
      // window.location.href = checkoutUrl;
    } catch (error) {
      toast.error(error.message || 'Checkout balatlamad');
    } finally {
      setLoading(null);
    }
  };

  return (
    <div className="space-y-6 animate-fade-in">
      <div className="text-center max-w-2xl mx-auto">
        <h1 className="text-3xl font-bold tracking-tight">Abonelik Planlar</h1>
        <p className="text-muted-foreground mt-2">
          htiyalarnza en uygun plan sein. stediiniz zaman ykseltme veya drn.
        </p>
      </div>

      <div className="grid gap-6 md:grid-cols-3 mt-8">
        {plans.map((plan, idx) => (
          <Card
            key={idx}
            className={`relative transition-all hover:shadow-lg flex flex-col ${
              plan.highlighted
                ? 'border-primary shadow-md scale-105'
                : ''
            }`}
          >
            {plan.highlighted && (
              <div className="absolute -top-3 left-0 right-0 flex justify-center">
                <span className="bg-primary text-primary-foreground text-xs font-semibold px-3 py-1 rounded-full">
                  En Popler
                </span>
              </div>
            )}
            <CardHeader>
              <CardTitle className="text-2xl">{plan.name}</CardTitle>
              <CardDescription className="min-h-[40px]">{plan.description}</CardDescription>
              <div className="mt-4">
                <span className="text-4xl font-bold">{plan.price}</span>
                <span className="text-muted-foreground">{plan.period}</span>
              </div>
            </CardHeader>
            <CardContent className="flex-1 flex flex-col">
              <ul className="space-y-3 flex-1">
                {plan.features.map((feature, featureIdx) => (
                  <li key={featureIdx} className="flex items-start gap-2">
                    <Check className="h-5 w-5 text-success flex-shrink-0 mt-0.5" />
                    <span className="text-sm">{feature}</span>
                  </li>
                ))}
              </ul>
              <Button
                className="w-full mt-6"
                variant={plan.highlighted ? 'default' : 'outline'}
                onClick={() => handleSelectPlan(plan.name)}
                disabled={loading === plan.name.toLowerCase()}
              >
                {loading === plan.name.toLowerCase() 
                  ? 'leniyor...' 
                  : plan.name === 'Enterprise' 
                    ? 'letiime Ge' 
                    : 'Plan Se'}
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>

      <Card className="mt-8">
        <CardHeader>
          <CardTitle>Ska Sorulan Sorular</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <h3 className="font-medium mb-1">Planm istediim zaman deitirebilir miyim?</h3>
            <p className="text-sm text-muted-foreground">
              Evet, istediiniz zaman plannz ykseltebilir veya drebilirsiniz.
            </p>
          </div>
          <div>
            <h3 className="font-medium mb-1">deme yntemleri nelerdir?</h3>
            <p className="text-sm text-muted-foreground">
              Kredi kart ve banka havalesi ile deme yapabilirsiniz.
            </p>
          </div>
          <div>
            <h3 className="font-medium mb-1">Deneme sresi var m?</h3>
            <p className="text-sm text-muted-foreground">
              Pro plan iin 14 gn cretsiz deneme imkan sunuyoruz.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
